.speech-bubble-left, .speech-bubble-right, .speech-bubble-top, .speech-bubble-bottom,.speech-bubble-bottom-right {
    position: relative;
    background: #ffffff;
    border-radius: .4em;
}

.speech-bubble-top:after {
    content: '';
    position: absolute;
    top: 0;
    left: 10%;
    width: 0;
    height: 0;
    border: 15px solid transparent;
    border-bottom-color: #ffffff;
    border-top: 0;
    margin-left: -15px;
    margin-top: -15px;
}
.speech-bubble-left:after {
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    width: 0;
    height: 0;
    border: 18px solid transparent;
    border-right-color: #ffffff;
    border-left: 0;
    margin-top: -18px;
    margin-left: -18px;
}
.speech-bubble-right:after {
    content: '';
    position: absolute;
    right: 0;
    top: 50%;
    width: 0;
    height: 0;
    border: 18px solid transparent;
    border-left-color: #ffffff;
    border-right: 0;
    margin-top: -18px;
    margin-right: -18px;
}
.speech-bubble-bottom:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 0;
    height: 0;
    border: 18px solid transparent;
    border-top-color: #ffffff;
    border-bottom: 0;
    margin-left: -18px;
    margin-bottom: -18px;
}.speech-bubble-bottom-right:after {
     content: '';
     position: absolute;
     bottom: 0;
     left: 50%;
